	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/06/crh0624001.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"crh0624.jpg: \" /></p>\n<p>Time to catch up</p>\n<p><b>Das Spiel ist aus!</b> In Euro 2000, Germany lost their final group game to Portugal 3-0 on Tuesday. They finished last in the group with no wins, and have gone home. The last time Germany finished a major tournement without a win was in 1938. As would be expected in a country where arrogance is a national attribute, the media is treating this as a major disaster. </p>\n<p><b>Shopping trip</b> Highlight of the day today was a trip to L端neburg to the Baby One shop to look for a crib and a baby phone. We would have bought the crib, they don't deliver, and to fit the crib in the Fiat one of us would have had to stay in L端neburg.</p>\n<p><b>It's Official</b> We'll be flying to the States for three weeks this fall to visit friends and family. We'll be in Arizona Sep 27 to Oct 3, then in Minnesota until Oct 17.</p>","fields":{"title":"Wet Weekend","date":"24 Jun 2000","url":"https://papascott.de/2000-06-24-wet-weekend/"}}},"pathContext":{"slug":"/2000-06-24-wet-weekend/","prev":{"fields":{"title":"Too Hot to Blog","date":"22 Jun 2000","slug":"/2000-06-22-too-hot-to-blog/"}},"next":{"fields":{"title":"Monday","date":"26 Jun 2000","slug":"/2000-06-26-monday/"}}}}

